<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>订单</el-breadcrumb-item>
                <el-breadcrumb-item>退款单</el-breadcrumb-item>
            </el-breadcrumb>
             <div class="add-menu fl-r">
                <el-button type="warning" class="add-menu-btn" @click="doAction('','add')" size="mini"  v-if="authOps.includes('/refund/create')">
                    <i class="iconfont icon-add"></i>新增退款单
                </el-button>
                    
            </div>
            
        </div>
        <!-- 新增退款单 -->
        <el-dialog title="新增退款单" :visible.sync="dialogAddForm" fullscreen>
          <el-form :model="addForm" label-width="120px" :inline="true">
            <el-form-item label="退款订单号" >
              <el-input v-model="addForm.orderCode" size="small"></el-input> 
            </el-form-item>
            <el-form-item  >
              <el-button size="small" type="primary" @click="getOrders">查询</el-button> 
            </el-form-item>
            <div class="tip" style="padding-left: 37px;color:#EA2000;">已支付订单才能创建退款单</div>            
          </el-form>
          <h4>基本信息</h4>
          <el-table :data="orderList" stripe style="width: 100%" v-loading="loading" class="report" max-height="520" border>
            <el-table-column label="订单号"  align="center" width="160" prop="orderCode" show-overflow-tooltip> </el-table-column> 
            <el-table-column label="订单状态"  align="center" width="160" prop="orderStatus" show-overflow-tooltip :formatter="format_status" > </el-table-column>
            <el-table-column label="会员"  align="center" width="200" prop="email" show-overflow-tooltip> </el-table-column>
            <el-table-column label="下单时间"  align="center" width="160">
              <template slot-scope="scope">{{scope.row.orderTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="来源订单号"  align="center" width="160" prop="sourceOrderCode" show-overflow-tooltip>
              
             </el-table-column>
            <el-table-column label="订单来源"  align="center" width="160" prop="orderSource" show-overflow-tooltip>
              <template slot-scope="scope">{{orderSourceName(scope.row.orderSource)}}</template>  
            </el-table-column>
            <el-table-column label="订单备注"  align="center" min-width="160" prop="note" show-overflow-tooltip> </el-table-column>
            <el-table-column label="系统备注"  align="center" min-width="160" prop="remark" show-overflow-tooltip> </el-table-column>
            <el-table-column label="业务备注"  align="center" min-width="160" prop="businessNote" show-overflow-tooltip> </el-table-column>
          </el-table>
          <h4>支付信息与收货人信息</h4>
          <el-table :data="orderList" stripe style="width: 100%" v-loading="loading" class="report" max-height="520" border>
             <el-table-column label="商城交易流水号"  align="center" width="160" show-overflow-tooltip> 
                <template slot-scope="scope">{{scope.row.payment[0].uniPayOrderCode}}</template> 
              </el-table-column> 
             <el-table-column label="交易流水号"  align="center" width="160"  show-overflow-tooltip > 
               <template slot-scope="scope">{{scope.row.payment[0].tradeNo}}</template>
             </el-table-column>
             <el-table-column label="支付时间"  align="center" width="140">
                <template slot-scope="scope">{{scope.row.payment[0].paymentTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="支付方式(渠道)名称"  align="center" width="150"  show-overflow-tooltip> 
              <template slot-scope="scope">{{scope.row.payment[0].paymentTypeName }}</template>
            </el-table-column>
            <el-table-column label="支付状态"  align="center" width="100" prop="paymentStatus" show-overflow-tooltip :formatter="format_status"> </el-table-column>
            <el-table-column label="收货人"  align="center" min-width="120" prop="contact" show-overflow-tooltip> </el-table-column>
            <el-table-column label="手机"  align="center" min-width="120" prop="tel" show-overflow-tooltip> </el-table-column>
            <el-table-column label="电话"  align="center" min-width="120" prop="mobile" show-overflow-tooltip> </el-table-column>
            <el-table-column label="邮编"  align="center" min-width="100" prop="postCode" show-overflow-tooltip> </el-table-column>
            <el-table-column label="详细地址"  align="center" min-width="160" prop="address" show-overflow-tooltip> </el-table-column>
            <el-table-column label="收件地区"  align="center" min-width="160" show-overflow-tooltip> 
              <template slot-scope="scope">
                {{scope.row.country+';'+scope.row.province+';'+scope.row.city}}
              </template>
            </el-table-column>
            <el-table-column label="邮编"  align="center" min-width="160" prop="postCode" show-overflow-tooltip> </el-table-column>
          </el-table>
          <h4>商品信息</h4>
          <el-table :data="orderItems" stripe style="width: 100%" v-loading="loading" @selection-change="selectSku" class="report"  border>
            <el-table-column type="selection" width="60"></el-table-column>
            <el-table-column label="商品名称"  align="center" min-width="250"  show-overflow-tooltip> 
              <template slot-scope="scope">
                <div class="sku-box">
                  <img :src="scope.row.skuImage" alt="" width="100" height="100">
                  <div class="info">
                    <p>{{scope.row.skuName}}</p>
                    <p v-for="(item,index) in JSON.parse(scope.row.skuAttr)" :key="index">
                      {{index}}:{{item}}
                    </p>
                  </div>
                </div>
              </template>
            </el-table-column>
            <el-table-column label="SKU编码"  align="center" min-width="120" prop="skuCode" show-overflow-tooltip> </el-table-column>
            <!-- <el-table-column label="SKU货号"  align="center" min-width="120" prop="skuName" show-overflow-tooltip> </el-table-column> -->
            <el-table-column label="商品售价(商家)"  align="center" min-width="100"  show-overflow-tooltip> 
              <template slot-scope="scope">$ {{scope.row.vendorPrice?(scope.row.vendorPrice/100).toFixed(2):''}}</template>
            </el-table-column>
            <el-table-column label="商品运费"  align="center" min-width="100"  show-overflow-tooltip> 
              <template slot-scope="scope">$ {{(scope.row.skuFreight/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column label="商品售价(用户)"  align="center" min-width="100"  show-overflow-tooltip> 
              <template slot-scope="scope"> {{scope.row.localPrice?(scope.row.localPrice/100).toFixed(2):''}}(当地{{orderList[0].payCurrencyCode}})</template>
            </el-table-column>
            <el-table-column label="数量"  align="center" min-width="160"  show-overflow-tooltip>
                  <template slot-scope="scope">
                    <el-input-number v-model="scope.row.qty"  :min="0" :max="scope.row.qtyMax" :step="1" @change="computeOrderNum"></el-input-number>
                  </template>
            </el-table-column>
            <el-table-column label="小计"  align="center" min-width="160" show-overflow-tooltip> 
              <template slot-scope="scope">$ {{scope.row.vendorPrice?((scope.row.skuFreight?(scope.row.skuFreight+scope.row.vendorPrice):scope.row.vendorPrice)*scope.row.qty/100).toFixed(2):''}}</template>
            </el-table-column>
          </el-table>
          <h4>退款单</h4>
          <el-form ref="refundForm" :model="addForm" label-width="150px" class="addForm-1" v-if="orderList.length!=0" >
            <el-form-item label="退款订单号:">
              <span>{{addForm.orderCode}}</span>
            </el-form-item>
            <el-form-item label="退款金额(USD):"  prop="refundMoney_m"  :rules="[{ required: true, message: '该选项必须填写' }]">
              <el-input v-model="addForm.refundMoney_m"  size="small"></el-input>
            </el-form-item>
            
            <el-form-item :label="'退款金额(当地'+orderList[0].payCurrencyCode+'):'" >
              <el-input :value="(addForm.refundMoney_m*orderList[0].localOrderAmount/(orderList[0].vendorOrderAmount+orderList[0].freight)).toFixed(2)"  size="small" :disabled="true"></el-input>
              <!-- <span>{{(addForm.refundMoney_m*orderList[0].exchangeRate).toFixed(2)}}</span> -->
              <!-- {{scope.row.localRefundMoney?(scope.row.localRefundMoney/100).toFixed(2):''}}({{scope.row.payCurrencyCode}}) -->
            </el-form-item>
            <!-- prop="refundNumber"  :rules="[{ required: true, message: '该选项必须填写'}]" -->
            <el-form-item label="退款数量:"  >
              <el-input v-model="addForm.refundNumber"  size="small"></el-input>
            </el-form-item>
            <el-form-item label="退款单类型:" prop="refundType" :rules="{ required: true, message: '该选项必须填写' }">
                <el-select v-model="addForm.refundType" size="small" clearable >
                  <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.refundType" :key="item.val"></el-option>
                </el-select>
            </el-form-item>
            
            <el-form-item label="退款原因:" prop="refundReason" :rules="{ required: true, message: '该选项必须填写' }" >
                <el-select v-model="addForm.refundReason" size="small" clearable>  
                    <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.refundReason" :key="item.val"></el-option>          
                </el-select>
            </el-form-item>
            <el-form-item label="退款单状态" prop="refundStatus" :rules="[{ required: true, message: '该选项必填' }]">
              <el-select v-model="addForm.refundStatus" size="small" clearable>
                  <el-option value="-1" label="作废" ></el-option>
                  <el-option value="0" label="待退款" ></el-option>
                  <el-option value="1" label="退款中" ></el-option>
                  <el-option value="2" label="退款失败" ></el-option>
                  <el-option value="3" label="已退款完成" ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="是否正常结算:" prop="isSettlement"  :rules="{ required: true, message: '该选项必须填写', trigger: 'blur' }">
                <el-select v-model="addForm.isSettlement" size="small" clearable>
                    <el-option value="1" label="是" ></el-option>
                    <el-option value="0" label="否" ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="责任方:" prop="responsibility" :rules="[{ required: true, message: '该选项必填' }]">
              <el-select v-model="addForm.responsibility" size="small" clearable>  
                    <el-option value="商家" label="商家" ></el-option>
                    <el-option value="物流" label="物流" ></el-option>
                    <el-option value="自有平台" label="自有平台" ></el-option>
                    <el-option value="客户" label="客户" ></el-option>
                    <el-option value="第三方平台" label="第三方平台" ></el-option>       
              </el-select>
            </el-form-item>            
            <el-form-item label="备注:">
              <el-input v-model="addForm.refundNote" size="small"></el-input>
            </el-form-item>
          
          </el-form>
          
          <div slot="footer" class="dialog-footer" style="text-align:center;">
            <el-button @click="dialogAddForm = false">取 消</el-button>
            <el-button type="primary" @click="submitAddForm" :disabled="stoken.create" :loading="stoken.create">确 定</el-button>
          </div>
        </el-dialog>
        <!--搜索-->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <!-- <i class="el-icon-search"></i> -->
                <div class="fold">
                    <span @click="fold?fold=false:fold=true">查询{{fold?'折叠':'展开'}}
                        <i :class="fold?'iconfont icon-fold-open':'iconfont icon-fold-close'"></i>
                    </span>
                </div>
                <transition name="el-zoom-in-center">
                    <div class="item-list" v-if="fold">                    
                       <el-form-item label="退款单类型">
                            <el-select v-model="searchForm.refundTypes" multiple size="small" clearable>
                                <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.refundType" :key="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                       <el-form-item label="退款原因">
                            <el-select v-model="searchForm.refundReasons" size="small" multiple clearable>
                                <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.refundReason" :key="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                       <el-form-item label="退款状态">
                            <el-select v-model="searchForm.refundStatuss" size="small" multiple clearable>
                                <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.refundStatus" :key="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="订单来源">
                            <el-select v-model="searchForm.orderSourceCodes"  size="small" clearable multiple>
                                <el-option :value="item.code" :label="item.name" v-for="item in cpaList" :key="item.code"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="订单号">
                            <el-input v-model="searchForm.orderCode" placeholder="订单号" size="small"></el-input>
                        </el-form-item>
                        
                        <el-form-item label="商家编码">
                            <el-input v-model="searchForm.supplierCode"  size="small"></el-input>
                        </el-form-item>

                        <el-form-item label="退款单号">
                            <el-input v-model="searchForm.refundCode"  size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="创建时间始">
                            <el-date-picker type="datetime" placeholder="选择日期" v-model="searchForm.createTimeBegin" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="创建时间止" class="">
                            <el-date-picker type="datetime" placeholder="选择日期" v-model="searchForm.createTimeEnd" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="完成时间始">
                            <el-date-picker type="datetime" placeholder="选择日期" v-model="searchForm.refundTimeBegin" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="完成时间止" class="">
                            <el-date-picker type="datetime" placeholder="选择日期" v-model="searchForm.refundTimeEnd" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>  
                        <el-form-item label="下单时间始">
                            <el-date-picker type="datetime" placeholder="选择日期" v-model="searchForm.orderTimeBegin" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="下单时间止" class="">
                            <el-date-picker type="datetime" placeholder="选择日期" v-model="searchForm.orderTimeEnd" :picker-options="pickerOptions"></el-date-picker>
                        </el-form-item>                  
                         <div class="action-bar">
                            <el-button type="primary" @click="search_smt">查询</el-button>
                            <el-button type="warning" @click="export_smt">
                                <i class="iconfont icon-export"></i>导出
                            </el-button>
                            <el-upload      
                            class="upload-button"
                            action="/proxy/ord/refund/import/excel"
                            :accept="'file/file.xls'"
                            :on-success="handleSuccess"
                            :before-upload='handldBeforeUpload'
                            :show-file-list="false"
                            >
                                <el-button size="small" type="success" :loading="importLoading" :disabled="importLoading">导入</el-button>
                                
                            </el-upload>
                            <a href="/proxy/fv/download/refunds_import.xls" style="color:#409EFF;margin-left:15px;" target="_blank" download>下载导入模板</a>

                        </div>      
                    </div>
                </transition>

            </el-form>
        </div>
        <div class="form-download" style="display:none;">
          <iframe src="" frameborder="0" name="msg-iframe" ></iframe>
          <form action="/proxy/ord/refund/export/excel" method="post" id="form-download" style="display:none;" target="msg-iframe">
              <input type="text" name="data"  id="form-data" :value="downloadkey"/>
              <button type="submit" >submit</button>
          </form>         
        </div>        
        <!-- 批量操作 -->
        <div class="doAction">
          <el-button type="primary" @click="doBatch('status')" size="small" v-if="authOps.includes('/freight/batch/edit')">批量修改已退款完成</el-button>
          <!-- /freight/batch/edit -->        
        </div>
        <!--列表-->
        <el-table :data="reportList" stripe style="width: 100%" v-loading="loading" class="report" max-height="520" border @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="40" fixed="left"></el-table-column> 
            <el-table-column label="订单号"  align="center" width="160" prop="orderCode" fixed="left" show-overflow-tooltip> </el-table-column>
            <el-table-column label="退款单号"  align="center" width="160" prop="refundCode" show-overflow-tooltip >  </el-table-column>
            <el-table-column label="退款单创建时间"  align="center" width="160">
                <template slot-scope="scope">{{scope.row.createTime|dateFormat }}</template>
            </el-table-column>
            <el-table-column label="订单支付时间"  align="center" width="160">
                <template slot-scope="scope">{{scope.row.paymentTime|dateFormat }}</template>
            </el-table-column>      
            <el-table-column label="订单下单时间"  align="center" width="160">
                <template slot-scope="scope">{{scope.row.orderTime|dateFormat }}</template>
            </el-table-column>      
           <el-table-column label="订单金额(商家USD)"  align="right" width="160" show-overflow-tooltip> 
             <template slot-scope="scope">{{scope.row.orderAmount?(scope.row.orderAmount/100).toFixed(2):''}}</template>
           </el-table-column>
           <el-table-column label="订单金额(当地币种)"  align="right" width="160" show-overflow-tooltip> 
             <template slot-scope="scope">
               {{scope.row.localPayAmount?(scope.row.localPayAmount/100).toFixed(2):''}}({{scope.row.payCurrencyCode}})
             </template>
           </el-table-column>
           <el-table-column label="支付时汇率"  align="right" width="160" show-overflow-tooltip> 
             <template slot-scope="scope">{{scope.row.exchangeRate}}</template>
           </el-table-column>
           <el-table-column label="退款金额(当地币种)"  align="center" width="160"  show-overflow-tooltip> 
             <template slot-scope="scope"><span v-if="scope.row.localRefundMoney">{{scope.row.localRefundMoney?(scope.row.localRefundMoney/100).toFixed(2):''}}({{scope.row.payCurrencyCode}})</span></template>
           </el-table-column>
           <el-table-column label="退款金额(USD)"  align="center" width="160"  show-overflow-tooltip> 
             <template slot-scope="scope">{{scope.row.orderAmount?(scope.row.refundMoney/100).toFixed(2):''}}</template>
           </el-table-column>
           <el-table-column label="商家编号"  align="center" width="160" prop="supplierCode" show-overflow-tooltip> </el-table-column>
           <el-table-column label="退款单类型" prop="refundType" align="center" width="150" :formatter="format_status" show-overflow-tooltip></el-table-column>
           <el-table-column label="退款状态" prop="refundStatus" align="center" width="150" :formatter="format_status" show-overflow-tooltip></el-table-column>
           <el-table-column label="退款原因" prop="refundReason" align="center" width="150" :formatter="format_status" show-overflow-tooltip></el-table-column>
          <el-table-column label="订单来源"  align="center" width="160" prop="orderSourceName" show-overflow-tooltip> </el-table-column>
          <el-table-column label="来源订单号"  align="center" width="160" prop="sourceOrderCode" show-overflow-tooltip> </el-table-column> 
          <el-table-column label="退款完成时间"  align="center" width="160">
                <template slot-scope="scope">{{scope.row.refundTime|dateFormat }}</template>
          </el-table-column>
          <el-table-column label="支付渠道编码"  align="center" width="160" prop="paymentChannelCode" show-overflow-tooltip> </el-table-column>
          <el-table-column label="责任方"  align="center" width="160" prop="responsibility" show-overflow-tooltip> </el-table-column>
          <el-table-column label="交易流水号"  align="center" width="160" prop="tradeNo" show-overflow-tooltip> </el-table-column>   
          <el-table-column label="是否正常结算"  align="center" width="160"  show-overflow-tooltip>
            <template slot-scope="scope">{{scope.row.isSettlement?'是':'否'}}</template>   
          </el-table-column>
          <el-table-column label="操作退款的用户名"  align="center" width="160" prop="refundUserName" show-overflow-tooltip> </el-table-column>
          <el-table-column label="退款备注"  align="center" width="160" prop="refundNote" show-overflow-tooltip> </el-table-column>
          <el-table-column label="更新人"  align="center" width="160" prop="updateUser" show-overflow-tooltip> </el-table-column>
          <el-table-column label="退款单创建人"  align="center" width="160" prop="createUser" show-overflow-tooltip> </el-table-column>
          <el-table-column label="操作" align="left" width="250" fixed="right">
              <template slot-scope="scope">
                  <el-button type="primary"  @click="doAction(scope.row,'detail')" size="mini">明细</el-button>
                  <el-button type="primary"  @click="doAction(scope.row,'edit')" size="mini"  v-if="authOps.includes('/refund/modify')" :disabled="(scope.row.refundStatus==3||scope.row.refundStatus==-1)">修改</el-button>
                  <el-button type="danger"  @click="doAction(scope.row,'delete')" size="mini"  v-if="authOps.includes('/refund/modify')" >删除</el-button>                
              </template>
          </el-table-column>
        </el-table>
        <!-- 修改退款 -->
        <el-dialog title="修改退款单" :visible.sync="dialogEdit" width="50%">
          <el-form ref="editForm" :model="editForm" label-width="150px" class="editForm">
            <el-form-item label="退款单号">
              <el-input v-model="editForm.modifyRefunds[0].refundCode" :disabled="true" size="small"></el-input>
            </el-form-item>
            <el-form-item label="订单金额(商家USD)" >
              <el-input :value="editForm.modifyRefunds[0].orderAmount/100" :disabled="true" size="small"></el-input>
            </el-form-item>   
            <el-form-item label="退款金额(商家USD)" prop="modifyRefunds[0].refundMoney_m" :rules="[{ required: true, message: '该选项必填' },{ type: 'number', message: '该选项必须是数字' }]">
              <el-input v-model.number="editForm.modifyRefunds[0].refundMoney_m" size="small"></el-input>
            </el-form-item>  
            <el-form-item :label="'退款金额(当地'+editForm.modifyRefunds[0].payCurrencyCode+')'">
              <el-input :value="(editForm.modifyRefunds[0].refundMoney_m*editForm.modifyRefunds[0].localPayAmount/editForm.modifyRefunds[0].orderAmount).toFixed(2)" :disabled="true" size="small"></el-input>
              
            </el-form-item>
            <el-form-item label="退款单状态" prop="refundStatus" :rules="[{ required: true, message: '该选项必填' }]" class="item-required">
              <el-select v-model="editForm.refundStatus" size="small" >
                  <el-option value="-1" label="作废" ></el-option>
                  <el-option value="0" label="待退款" ></el-option>
                  <el-option value="1" label="退款中" ></el-option>
                  <el-option value="2" label="退款失败" ></el-option>
                  <el-option value="3" label="已退款完成" ></el-option>
              </el-select>
            </el-form-item>
           <el-form-item label="退款单类型" prop="modifyRefunds[0].refundType"  class="is-required" >
              <el-select v-model="editForm.modifyRefunds[0].refundType" size="small" >
                  <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.refundType" :key="item.val"></el-option>
              </el-select>
           </el-form-item>
           <el-form-item label="退款原因" prop="modifyRefunds[0].refundReason"  class="is-required" >
              <el-select v-model="editForm.modifyRefunds[0].refundReason" size="small" >
                  <el-option :value="item.val" :label="item.label" v-for="item in myConfig.status.refundReason" :key="item.val"></el-option>
              </el-select>
           </el-form-item>
            <el-form-item label="退款备注"  prop="modifyRefunds[0].refundNote" :rules="[{ required: true, message: '该选项必填' }]">
              <el-input v-model="editForm.modifyRefunds[0].refundNote" size="small"></el-input>
            </el-form-item>
            <el-form-item label="是否正常结算"  prop="modifyRefunds[0].isSettlement" :rules="[{ required: true, message: '该选项必填' }]">
              <el-select v-model="editForm.modifyRefunds[0].isSettlement" size="small" clearable>
                  <el-option value="0" label="否" ></el-option>
                  <el-option value="1" label="是" ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="责任方"  prop="modifyRefunds[0].responsibility" :rules="[{ required: true, message: '该选项必填' }]">
              <el-select v-model="editForm.modifyRefunds[0].responsibility" size="small" clearable>  
                    <el-option value="商家" label="商家" ></el-option>
                    <el-option value="物流" label="物流" ></el-option>
                    <el-option value="自有平台" label="自有平台" ></el-option>
                    <el-option value="客户" label="客户" ></el-option>
                    <el-option value="第三方平台" label="第三方平台" ></el-option>       
              </el-select>
            </el-form-item>
            
          </el-form>
          <div slot="footer" class="dialog-footer" style="text-align:center;">
            <el-button @click="dialogEdit = false">取 消</el-button>
            <el-button type="primary" @click="submitEditForm">确 定</el-button>
          </div>
        </el-dialog>

        <!-- 退款单明细 -->
        <el-dialog title="退款单明细" :visible.sync="dialogOrderDetail">
          <el-table :data="orderDetail" class="report" border>
            <el-table-column prop="skuCode" label="SKU编码" width="160" show-overflow-tooltip></el-table-column>
            <el-table-column prop="skuName" label="SKU的名称" show-overflow-tooltip></el-table-column>
            <el-table-column prop="qty" label="SKU数量" width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="price" label="运费（USD）" width="120" show-overflow-tooltip align="right">
              <template slot-scope="scope">{{(scope.row.skuFreight/100).toFixed(2)}}</template>
            </el-table-column>
            <el-table-column prop="price" label="售价（USD）" width="100" show-overflow-tooltip align="right">
              <template slot-scope="scope">{{scope.row.price?(scope.row.price/100).toFixed(2):''}}</template>
            </el-table-column>
          </el-table>
        </el-dialog>
        <!-- 分页 -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

    </div>
</template>


<script>
import format from "date-format";
import { mapGetters } from 'vuex'

export default {
  data() {
    return {
      //
      stoken:{
        create:false
      },
      //用户信息
      userInfo:{},
      //多选数据
      selectData:[],
      //搜索折叠
      fold: true,
      searchForm: {
       createTimeBegin:"",
       createTimeEnd:"",
       refundTimeBegin:"",
       refundTimeEnd:"",
       refundCode:"",
       orderCode:"",
       refundTypes:[],
       refundReasons:[],
       orderSourceCodes:[],
       refundStatuss:[],
       supplierCode:"",
       orderTimeBegin:'',
       orderTimeEnd:'',
        page: {
          page: 1,
          pageSize: this.myConfig.pageRows
        }
      },        
      addForm:{
        orderCode:"",
        createUser:'',
        cancelType:'',
        refundNote:'',
        refundMoney:'',
        refundMoney_m:'',
        refundNumber:'',
        refundType:'',
        refundReason:'',
        payCurrencyCode:'',
        responsibility:'',
        isSettlement:'',
        refundStatus:'',
        freight:'',
        vendorOrderAmount:'',
      },
      pickerOptions: {
        disabledDate(time) {
          if (time.getTime() > Date.now()) {
            return time;
          } else {
            return false;
          }
        }
      },
      //报表列表
      loading: false,
      total: 0,
      reportList: [],
      // 合作商列表
      cpaList: [],
      orderDetail:[],
      dialogOrderDetail:false,
      dialogAddForm:false,
      orderList:[],
      orderItems:[],
      payCurrencyCode:'',
      selectSkuData:[],
      //修改退款单
      dialogEdit:false,
      editForm:{
        refundStatus: '',
        updateUser: "",
        modifyRefunds:[{
            responsibility:'',
            isSettlement:'',
            refundReason:'',
            refundType:'',
            refundCode: "",
            refundMoney: '',
            refundMoney_m: '',
            refundNote: "",
            exchangeRate:""
        }] 
      },
      importLoading:false,//导入loading,
      
      
    };
  },
  computed:{
    ...mapGetters({ 
            authOps: 'getAuthOps',           
        }),
      downloadkey(){
        return JSON.stringify(this.searchForm)
      }        
        
  },
  filters: {
    dateFormat(date) {
      return date ? format("yyyy-MM-dd hh:mm", new Date(date)) : "";
    }
  },
  methods: {
    orderSourceName(data){ 
      for(let i in this.cpaList){
        if(this.cpaList[i].code==data){
          return this.cpaList[i].name;
          break
        }
      }
      return 'test...'
    },
    //列表状态过滤
    format_status(row, column, cellValue) {
        for (let i in this.myConfig.status[column.property]) {
            if (this.myConfig.status[column.property][i].val == row[column.property]) {
                return this.myConfig.status[column.property][i].label;
                break;
            }
        }
        return row[column.property];
    },
    DateToString() {
      for (let i in this.searchForm) {
        //日期转换
        if (this.searchForm[i] instanceof Date) {
          this.searchForm[i] = format("yyyy-MM-dd hh:mm:ss",new Date(this.searchForm[i]));
        }
        //去掉前后空格
        if (this.searchForm[i] instanceof String) {
          this.searchForm[i] = this.searchForm[i].trim();
        }
      }
    },
    search_smt() {
      this.searchForm.page.page = 1;
      this.DateToString();
      this.getRefundList();
    },
    //报表列表获取
    getRefundList() {
      this.loading = true;
      this.axios.post( "/proxy/ord/refund/getRefunds",{ body: this.searchForm }).then(response => {
        let data = response.data;
        if (data.head.code == "10000") {
          this.loading = false;
          this.reportList = data.body.rows;
          this.total = data.body.total;
        }
      });
    },
    //分页
    handleCurrentChange(val) {
      this.searchForm.page.page = val;
      this.getRefundList();
    },
    doAction(data,type) {
      switch(type){
        //退款单明细
        case 'detail':
            this.orderDetail=data.items;
            this.dialogOrderDetail=true;
          break;
        //新增退款单
        case 'add':
            this.dialogAddForm=true;
            this.addForm={
              orderCode:"",
              createUser:JSON.parse(localStorage.getItem('userInfo')).email,
              cancelType:'',
              refundNote:'',
              refundMoney:'',
              refundMoney_m:'',
              refundNumber:'',
              refundReason:'',
              refundType:'',
              payCurrencyCode:'',
              responsibility:'',
              isSettlement:'',
              refundStatus:'',
              freight:'',
              vendorOrderAmount:'',
            };
            this.orderList=[];
            this.orderItems=[];
            this.selectSkuData=[];
          break;
        case 'edit':
          this.editForm.refundStatus=data.refundStatus+'';
          this.editForm.updateUser=JSON.parse(localStorage.getItem('userInfo')).email;
          this.editForm.modifyRefunds=[{
            responsibility:data.responsibility,
            isSettlement:data.isSettlement+'',
            refundReason:data.refundReason,
            refundType:data.refundType,
            refundCode:data.refundCode,
            exchangeRate:data.exchangeRate,
            orderAmount:data.orderAmount,
            localPayAmount:data.localPayAmount,
            payCurrencyCode:data.payCurrencyCode,
            refundMoney:(data.refundMoney/100).toFixed(2),
            refundMoney_m:Number((data.refundMoney/100).toFixed(2)),
            refundNote:data.refundNote
          }];

          this.dialogEdit=true;
          break;
        case 'delete'://删除功能
          this.$confirm('此操作将删除该退款单, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            let params={
                  updateUser:JSON.parse(localStorage.getItem('userInfo')).email,
                  refundCodes:[data.refundCode]
            }
            this.axios.post('/proxy/ord/refund/delete',{body:params}).then(res=>{
              let data=res.data;
              if(data.head.code=="10000"){
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
                this.getRefundList();                
              }
            })

          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });          
          });            
        break;  

      }
    },
    //获取订单信息
    getOrders(){
        this.addForm.orderCode=this.addForm.orderCode.trim();
        if(this.addForm.orderCode==''){
          this.$message({
            message: '请输入合法的已支付的订单号！',
            type: 'error',
            showClose:true,
            duration:2000
          });
          return false;
        }
        //新建退款单时，如果已经有重复的订单，需页面弹框提醒
        this.axios.post( "/proxy/ord/refund/getRefunds",{ body: {orderCode:this.addForm.orderCode,page:{page:1,pageSize:1000}} }).then(res => {
          let data=res.data;
          if(data.head.code=='10000'){         
            if(data.body.total>0){      
              this.$confirm('此订单已经存在退款单，是否再建退款单？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                this.axios.post('/proxy/ord/order/getOrders',{body:{orderCode:this.addForm.orderCode.trim(),paymentStatus:2}}).then(res=>{
                  let data = res.data;
                  if (data.head.code == "10000") {
                    this.orderList=data.body.orders;
                    this.selectSkuData=[];           
                    if(this.orderList.length==0){
                      this.orderItems=[];      
                      this.$message({
                        message: '请输入合法的已支付的订单号！',
                        type: 'error',
                        showClose:true,
                        duration:2000
                      });
                    }else{
                      this.orderItems=this.orderList[0].orderItems;
                      this.payCurrencyCode=this.orderList[0].payCurrencyCode;
                      for(let i in this.orderItems){
                        this.orderItems[i].qtyMax=this.orderItems[i].qty;
                      }         
                    }                   
                  }
                })             
              }).catch(() => {
                this.$message({
                  type: 'info',
                  message: '已取消新建'
                });          
              });
            }else{
                this.axios.post('/proxy/ord/order/getOrders',{body:{orderCode:this.addForm.orderCode.trim(),paymentStatus:2}}).then(res=>{
                  let data = res.data;
                  if (data.head.code == "10000") {
                    this.orderList=data.body.orders;
                    this.selectSkuData=[];           
                    if(this.orderList.length==0){
                      this.orderItems=[];      
                      this.$message({
                        message: '请输入合法的已支付的订单号！',
                        type: 'error',
                        showClose:true,
                        duration:2000
                      });
                    }else{
                      this.orderItems=this.orderList[0].orderItems;
                      this.payCurrencyCode=this.orderList[0].payCurrencyCode;
                      for(let i in this.orderItems){
                        this.orderItems[i].qtyMax=this.orderItems[i].qty;
                      }         
                    }                   
                  }
                })    
            }
           
            
            
          }
        })
        
    },
    getCpaList() {
      this.axios.post(this.myConfig.code.proxy + "/ec/cpa/partners/all", { body: {} }).then(response => {
        let data = response.data;
        if (data.head.code == "10000") {
          this.cpaList = data.body.list;
        }
      });
    },
    //选择退款sku
    selectSku(val){
      this.selectSkuData=val;
      this.computeOrderNum();
    },
    //计算退款单的金额与数量
    computeOrderNum(){
      this.timer=setTimeout(()=>{
        this.addForm.refundNumber='';
        this.addForm.refundMoney_m='';
        for(let i in this.selectSkuData){
          this.addForm.refundNumber=Number(this.addForm.refundNumber) +this.selectSkuData[i].qty;
          this.addForm.refundMoney_m=Number((Number(this.addForm.refundMoney_m)+this.selectSkuData[i].qty*(this.selectSkuData[i].vendorPrice+this.selectSkuData[i].skuFreight)/100).toFixed(2));
        }
      },350)
      
    },
    //提交新增退款单表单
    submitAddForm(){
      this.$refs.refundForm.validate((valid) => {
        if (valid) {
          let form={};
          this.addForm.refundMoney=Number((Number(this.addForm.refundMoney_m)*100).toFixed(2));
          this.addForm.localRefundMoney=Number((this.addForm.refundMoney_m*this.orderList[0].localOrderAmount/(this.orderList[0].vendorOrderAmount+this.orderList[0].freight)).toFixed(2))*100;
          // (addForm.refundMoney_m*orderList[0].localOrderAmount/(orderList[0].vendorOrderAmount+orderList[0].freight)).toFixed(2)
          form.items=this.selectSkuData;
          Object.assign(form,this.addForm);
          form.paymentTime=this.orderList[0].payment[0].paymentTime;
          form.orderAmount=this.orderList[0].orderAmount;
          form.paymentChannelCode=this.orderList[0].payment[0].paymentChannelCode;
          form.tradeNo=this.orderList[0].payment[0].tradeNo;
          form.orderSourceCode=this.orderList[0].orderSource;
          form.sourceOrderCode=this.orderList[0].sourceOrderCode;
          form.orderTime=this.orderList[0].orderTime;
          form.merchantCode=this.orderList[0].merchantCode;
          form.payCurrencyCode=this.orderList[0].payCurrencyCode;
          form.exchangeRate=this.orderList[0].exchangeRate;
          form.localPayAmount=this.orderList[0].localOrderAmount;
          form.freight=this.orderList[0].freight;
          form.vendorOrderAmount=this.orderList[0].vendorOrderAmount;
          if(this.selectSkuData.length==0){
              this.$message({
              message: '请选择要退款的sku!',
              type: 'error',
              showClose:true,
              duration:2000
            });
            return false;
          }
          this.stoken.create=true;
          this.axios.post('/proxy/ord/refund/create',{body:{refund:form}}).then(res=>{
            this.stoken.create=false;
            let data=res.data;
            if(data.head.code=='10000'){
              this.dialogAddForm=false;
              this.getRefundList();
            }
          })
        } 
      });
      return false;
    },
    // 提交修改退款单表单
    submitEditForm(){
      this.$refs.editForm.validate((valid)=>{
          if(valid){
            this.editForm.modifyRefunds[0].refundMoney=Number(this.editForm.modifyRefunds[0].refundMoney_m).toFixed(2)*100;
            this.editForm.modifyRefunds[0].localRefundMoney=Number((this.editForm.modifyRefunds[0].refundMoney_m*this.editForm.modifyRefunds[0].localPayAmount/this.editForm.modifyRefunds[0].orderAmount).toFixed(2))*100;
            this.axios.post('/proxy/ord/refund/modify',{body:this.editForm}).then(res=>{
              let data=res.data;
              if(data.head.code=='10000'){
                this.dialogEdit=false;
                this.$message('修改成功！')
                this.getRefundList();
              }
            })
          }else{
            return false;
          }
          
      })
    },
    //导出
    export_smt() {
        this.$confirm('您确定要导出？','提示' ,{
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'success'
        }).then(()=>{
             document.getElementById('form-download').submit();
        }).catch(()=>{
            this.$message({
                type: 'info',
                message: '已取消'
            }); 
        })        
       
    },
    
     //复选框
    handleSelectionChange(val) {
      this.selectData = val;
    },
    //批量修改状态
    doBatch(){
      let ids = [];
      for (let i in this.selectData) {
          ids.push({refundCode:this.selectData[i].refundCode});
      }
      if (ids.length == 0) {
        this.$message({
          message: "请选要操作的订单！",
          type: "error"
        });
        return false;
      } 
      this.axios.post('/proxy/ord/refund/modify',{body:{refundStatus:3,updateUser:this.userInfo.userName,modifyRefunds:ids}}).then((res)=>{
          let data=res.data;
          if(data.head.code=='10000'){
            this.$message({
              message: "修改成功！",
              type: "success"
            });
            this.getRefundList();
          }
      })
    },
    //获取用户信息
    getUserInfo(){
      this.axios.post('/proxy/auth/api/auth/loginUserInfo',{body: { token: this.$cookie.get("token") }}).then((res)=>{
        let data=res.data;
        if(data.head.code=='10000'){
          this.userInfo=data.body;
         
        }
      })
    },
    handldBeforeUpload(){
        this.importLoading=true;
    },
    handleSuccess(res){
        this.importLoading=false;
        if(res.head.code=='10000'){
            if(res.body.code=='0'){
              this.$message.success("导入成功！");
            }else{
              let _html=res.body.msg;
              this.$alert(`<div>${_html}</div>`, '错误提示', {
                  dangerouslyUseHTMLString: true,
                  customClass:'message-import'
              });
            }
            this.getRefundList();
            
        }else{
            this.$message.error(res.body.sub_msg)
        }
    },    
  },
  created() { 
    this.getUserInfo();
    this.getCpaList();
    this.getRefundList();
    
  }
};
</script>


<style lang="less" scoped>
.wrap {
  padding-right: 40px;
  .sku-box{
    clear: both;
    img{
      float: left;
    }
    .info{
      padding-left: 110px;
      text-align: left;
    }
  }
}
</style>
<style lang="less">
.upload-button{
  display: inline-block;
}
.addForm-1,.editForm{
  .el-input__inner{
    width:400px;
  }
}
.el-form-item {
    margin-bottom: 12px;
}
.el-form-item__error{
  padding-top:1px;
}
.el-input.is-disabled .el-input__inner{
  color:#777;
}
.message-import .el-message-box__message{
    max-height: 300px;
    overflow-y: auto;
}
</style>





